.box-activity {
    padding: 0px 16px 16px 16px;
    background-color: var(--white);
}

.nav-activity {
    width: 100%;
    height: 31px;
    display: flex;
    justify-content: space-between;
}

.text-activity {
    font-size: 24px;
    font-family: Bold;
    color: var(--grey1);
}

.history {
    margin-top: 4px;
    width: 24px;
    height: 24px;
}

.button-head {
    margin-top: 16px;
    display: flex;
    flex-direction: row;
    font-size: 16px;
    font-family: bold;
}

.head-box {
    width: 50%;
    height: 39px;
    cursor: pointer;

}



.head-text {
    color: var(--neutralGrey3);
    text-align: center;
}

.head-active {
    color: var(--persianBlue);
    text-align: center;
}

.active-hr-button {
    width: 100%;
    height: 2px;
    background-color: var(--persianBlue);
    margin: 0 !important;
}

.hr-button {
    width: 100%;
    height: 2px;
    background-color: var(--neutralGrey3);
    margin: 0 !important;
}